#portfolio {
    width: 100%;
    background: $lighter-gray;
}


// Card Gallery
#gallery {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.gallery-card {
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 1200px;
    max-height: 450px;
    border-radius: 10px;
    margin: 2rem;
    background: $white;
    box-shadow: rgba(0, 0, 0, 0.18) 0px 2px 4px;
}

.card-img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50%;
    max-height: 450px;
    border-radius: 10px 0 0 10px;
    overflow: hidden;
    
    .card-gallery {
        position: relative;
    }

    img {
        min-width: 100%;
        min-height: 100%;
        border-radius: 5px 0 0 5px;
        transition: transform .2s;

        &:hover {
            cursor: pointer;
            opacity: 0.8;
            transform: scale(1.1);
        }
    }
}


// Card Description

.card-descript {
    background: $white;
    box-sizing: border-box;
    width: 50%;
    padding: 1rem 1rem 0 1rem;
    border-radius: 0 10px 10px 0;
}


@media (max-width: 750px) {
    .gallery-card {
        flex-flow: column;
        margin: 2rem 0;
    }

    .card-img {
        width: 100%;
        border-radius: 10px 10px 0 0;

        img {
            border-radius: 10px 10px 0 0;
        }
    }

    .card-descript {
        border-radius: 0 0 10px 10px;
        width: 100%;
    }
}









// Modal
/* The Modal (background) */
.modal-gallery {
    display: block;
    position: fixed;
    z-index: 5;
    padding-top: 100px;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.90);
}


/* Modal Content */
.modal-content {
    position: absolute;
    bottom: 100px;
    display: flex;
    flex-flow: row wrap;
    margin: 0 auto;
    text-align: center;
    padding: 0;
    width: 100%;
}

.current-slide {
    margin: 0 auto;
    text-align: center;

    img {
        max-height: 550px;
        max-width: 100%;
        object-fit: cover;
    }
}

/* Slideshow Thumbnails */
.slideshow {
    background: $black;
    width: 20%;
    overflow: hidden;

    img {
        max-width: 100%;
        height: 180px;
        object-fit: cover;
        opacity: 0.6;

        &:hover {
            opacity: 1;
            transition: 0.3s;
        }
    }
}

/* The Close Button */
.close {
color: white;
position: absolute;
top: 70px;
right: 25px;
font-size: 6em;
font-weight: bold;
}

.close:hover,
.close:focus {
color: #999;
text-decoration: none;
cursor: pointer;
}