#about {
    max-width: 100%;
    padding: $primary-page-pad;
    background: $white;
}

.about-content {
    max-width: 1200px;
    margin: 0 auto;

    p {
        font-size: 3em;
    }
    
    ul {
        font-size: 2.2em;
        list-style-position: outside;
    }
}

.about-services {
    font-size: 2vw;
    text-align: center;
}

@media (min-width: 200px) {
    .about-content {
        p {
            font-size: 2em;
        }

        ul {
            font-size: 2em;
        }
    }
    .about-services {
        font-size: 5vw;
    }
}

@media (min-width: 750px) {
    .about-services {
        font-size: 3vw;
    }
}

@media (min-width: 1920px) {
    .about-services {
        font-size: 1.5vw;
    }
}
