/** 
VARIABLES
**/

/* Basic */

$white: #fff;
$black: #000;
$darker-gray: #222;
$dark-gray: #333;
$gray: #555;
$light-gray: #888;
$lighter-gray: #e0e0e0;

/* Primary */

$primary-yellow: rgb(244, 255, 92);
$primary-teal: rgb(116, 242, 203);

// Page Padding

$primary-page-pad: 100px 6rem;

@media (max-width: 750px) {
    $primary-page-pad: 80px 2rem;
}