// anchor link offset for scrolling
a.anchor{display: block; position: relative; top: -80px; visibility: hidden;}


/**
Navigation Bar
**/

#nav-header {
    height: 60px;
    width: 100%;
    max-width: 100vw;
    padding: 0 6rem ;
    margin-bottom: 1rem;
    box-sizing: border-box;
    box-shadow: rgba(0, 0, 0, 0.18) 0px 2px 4px;
    background: $white;
    z-index: 500;
}

.nav-wrapper {
    height: 100%;
    width: 100%;
    margin: 0 auto;
    display: block;
}

.nav-content {
    padding: 16px;
}

.nav-sticky {
    position: fixed;
    top: 0;
    width: 100%;
}

.nav-sticky + #hero {
    padding-top: 102px;
}

// Navigation Links

.nav-logo {
    float: left;
    line-height: 60px;
    font-weight: bold;
    font-size: 2em;

    a {
        color: $dark-gray;
    }

    &:hover {
        color: $primary-teal;
    }
}

.nav-links {
    float: right;
    display: inline;

    ul {
        display: table-cell;
        vertical-align: middle;
        height: 60px;
        font-weight: bold;

        li {
            display: inline;
            margin-left: 30px;
            cursor: pointer;

            a {
                text-decoration: none;
                color: $dark-gray;
                transition: 200ms;
                font-size: 1.5em;


                &:hover {
                    color: $primary-teal;
                }
            }
        }
    }
}

.social-link {
    height: 40px;
    width: 40px;
    margin-left: 20px !important;
}

#linkedin-icon {
    background: url(../images/social/linkedin.png) no-repeat;
    display: inline-block;
    vertical-align: middle;
    height: 40px;
    width: 40px;

    &:hover {
        background-position: 0 -40px;
    }
}

@media (max-width: 750px) {
    #nav-header {
        padding: 0 2rem;
    }

    .nav-logo {
        a {
            font-size: 5vw;
        }
    }
}